'use client';

import { useEffect, useRef } from 'react';

interface MathRendererProps {
  content: string;
  className?: string;
}

export default function MathRenderer({ content, className = '' }: MathRendererProps) {
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (containerRef.current) {
      // KaTeX„ÅåÂà©Áî®ÂèØËÉΩ„Å™Â†¥Âêà„ÅØÊï∞Âºè„Çí„É¨„É≥„ÉÄ„É™„É≥„Ç∞
      if (typeof window !== 'undefined' && (window as any).katex) {
        const katex = (window as any).katex;
        
        // „Ç§„É≥„É©„Ç§„É≥Êï∞ÂºèÔºà$...$Ôºâ„ÇíÂá¶ÁêÜ
        let processedContent = content.replace(/\$([^$]+)\$/g, (match, formula) => {
          try {
            return katex.renderToString(formula, { displayMode: false });
          } catch (error) {
            console.warn('KaTeX rendering error:', error);
            return match;
          }
        });

        // „Éá„Ç£„Çπ„Éó„É¨„Ç§Êï∞ÂºèÔºà$$...$$Ôºâ„ÇíÂá¶ÁêÜ
        processedContent = processedContent.replace(/\$\$([^$]+)\$\$/g, (match, formula) => {
          try {
            return katex.renderToString(formula, { displayMode: true });
          } catch (error) {
            console.warn('KaTeX rendering error:', error);
            return match;
          }
        });

        containerRef.current.innerHTML = processedContent;
      } else {
        // KaTeX„ÅåÂà©Áî®„Åß„Åç„Å™„ÅÑÂ†¥Âêà„ÅØ„ÄÅÊï∞ÂºèË®òÂè∑„Çí„Åù„ÅÆ„Åæ„ÅæË°®Á§∫
        containerRef.current.innerHTML = content;
      }
    }
  }, [content]);

  return (
    <div 
      ref={containerRef}
      className={`math-content ${className}`}
      style={{ 
        lineHeight: '1.6',
        wordBreak: 'break-word'
      }}
    />
  );
}